import Calendar from 'react_google_calendar';
import React from 'react';

const calendar_configuration = {
    api_key: 'AIzaSyDahTZUtTKORUdsOY3H7BEeOXbwye0nBHI',
    calendars: [
      {
        name: 'Calendar', // whatever you want to name it
        url: 'https://calendar.google.com/calendar/embed?src=oxfordunichess%40gmail.com&ctz=Europe%2FLondon' // your calendar URL
      }
    ],
    dailyRecurrence: 700,
    weeklyRecurrence: 500,
    monthlyRecurrence: 20
}

export default class MyApp extends React.Component {
    constructor(props) {
      super(props)
        this.state = {
          events: []
        }
    }

    render = () =>
      <div>
        <Calendar
          events={this.state.events}
          config={calendar_configuration} />
      </div>
}